import { RouterModule, Routes } from '@angular/router';
import { NgModule } from '@angular/core';
import { ProjectInfoComponent } from '@app/ffufm-feedback-dashboard-features/project-info/project-info.component';

const projectInfoRoutes: Routes = [
  {
    path: '',
    component: ProjectInfoComponent
  }
];

@NgModule({
  declarations: [],
  imports: [
    RouterModule.forChild(projectInfoRoutes)
  ],
  exports: [RouterModule]
})

export class ProjectInfoRouting {}


