import { ActivatedRoute } from '@angular/router';
import { Answer } from '@app/shared/models/answer.model';
import { AnswerStore, ProjectStore, QuestionStore } from '@app/core/stores';
import { Component, OnInit } from '@angular/core';
import { Project } from '@app/shared/models/project.model';
import { Question } from '@app/shared/models/question.model';

@Component({
  selector: 'ffufm-feedback-dashboard-project-info',
  templateUrl: './project-info.component.html',
  styleUrls: ['./project-info.component.css']
})
export class ProjectInfoComponent implements OnInit {

  projectId: string;
  project: Project;
  answers: Answer[];
  questions: Question[];

  constructor(private route: ActivatedRoute,
              private projectStore: ProjectStore,
              private answerStore: AnswerStore,
              private questionStore: QuestionStore) { }

  ngOnInit() {
    this.route.params.subscribe(params => {
      this.projectId = params.projectId;
      this.getProject();
      this.getAnswers();
      this.getQuestions();
    });
  }

  getProject() {
    this.projectStore.getProjects()
        .subscribe((projects: Project[]) => {
          projects.forEach(project => {
            if (project.id === this.projectId) {
              this.project = project;
            }
          });
        });
  }

  getAnswers() {
    this.answers = [];
    this.answerStore.getAnswers()
        .subscribe((answers: Answer[]) => {
          answers.forEach((answer) => {
            if (answer.project_id.toString() === this.projectId) {
              this.answers.push(answer);
            }
          });
        });
  }

  getQuestions() {
    this.questionStore.getQuestions()
        .subscribe((questions: Question[]) => {
          this.questions = questions;
        });
  }

}



